body {
  background: white;
  font-family: 'Pacifico';
  fontisize: 16px; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.about {
  position: relative;
  grid-area: about; }
  .about__header {
    color: #FF729F;
    font-family: 'Pacifico', cursive; }
  .about__link {
    color: #f02f6c;
    font-size: 13px; }
  .about__paragraph {
    padding: 16px;
    font-size: 20px; }
    .about__paragraph--color {
      color: #055d80;
      font-size: 13px; }
  .about__picture {
    width: 80%;
    max-width: 150px;
    display: block;
    float: left;
    margin: 0 16px 16px 0; }

.box, .boxtest {
  width: 100px;
  height: 100px;
  background: #81F4E1;
  padding: 10px;
  border: 10px solid #30011E;
  box-sizing: border-box;
  position: sticky;
  top: 0px; }

.content {
  grid-area: content; }

.day:first-child {
  background: #ebe3e3; }

.day__header {
  color: #FF729F;
  margin: 0 0 16px;
  font-family: 'Pacifico', cursive; }

.day__header::after {
  content: " • ";
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  margin-left: 10px; }

.day__header::before {
  content: " • ";
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  margin-right: 5px; }

.diary {
  clear: both; }
  .diary__header {
    color: #FF729F;
    font-family: 'Pacifico', cursive; }

.focus:focus {
  border: 3px solid #FF729F; }

.footer {
  font-size: 12px; }
  .footer__top {
    position: fixed;
    bottom: 25px;
    right: 10px;
    color: #30011E; }

.glossary
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 16px; }

.header {
  grid-area: header; }

.hobby__list {
  list-style: none;
  padding: 0; }

.main {
  width: 100%;
  max-width: 1400px;
  margin: 32px auto;
  padding-top: 70px;
  display: grid;
  grid-template-areas: "header header" "about content";
  grid-template-columns: 250px 1fr; }
  .main__header {
    font-size: 32px;
    color: #30011E;
    margin-bottom: 25px;
    grid-area: header;
    font-family: 'Pacifico', cursive; }

@media (max-width: 640px) {
  .main {
    grid-template-areas: "header" "content" "about";
    grid-template-columns: 1fr; } }

.navigation__item {
  display: block;
  margin: 16px 16px; }

.navigation__link {
  color: #FF729F;
  font: bold;
  font-size: 15px; }
  .navigation__link:hover {
    color: #D6A4A4; }
  .navigation__link:visited {
    color: #ada6a9; }

.navigation__list {
  list-style: none;
  padding: 16px 16px;
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 20px; }

.navigation__switcher {
  border: none;
  background: none;
  position: absolute;
  right: 16px;
  top: 16px; }

.site-header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  box-sizing: border-box;
  padding: 16px;
  background: #ffffff;
  z-index: 10;
  min-height: 48px; }

.table {
  width: 100%;
  background-color: #FF729F;
  border-collapse: collapse;
  border: 3px solid #30011E; }
  .table__header {
    color: #30011E;
    font-size: 20px;
    font-weight: bold; }

@media not screen and (min-width: 640px) and (max-width: 1400px) {
  .grid {
    grid-template-columns: 1fr; }
  .day__link {
    color: #30011E; } }

@media (min-width: 641px) {
  .main {
    max-width: 900px; }
  .navigation__switcher {
    display: none; } }

@media (max-width: 640px) {
  .navigation__list {
    flex-direction: column;
    display: none; }
  .navigation__list--visable {
    display: flex; }
  .navigation_item {
    margin: 16px; } }
